@charset "UTF-8";

.product {
  display: block;
  width: 100%;
  height: 660px;
  background: url("../assets/header-bg.png") no-repeat, #1f2147;
  /*______________MEDIA RULES________________*/
}

.product__background {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  padding-top: 10px;
  background-color: #000000;
}

.product__navigation {
  display: flex;
  flex-wrap: wrap;
  max-width: 1170px;
  margin: 0 auto;
  color: #a9abaf;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.product__logo {
  display: block;
  width: 189px;
  height: 45px;
}

.product__advantages-list {
  display: block;
  padding-left: 0;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}

.product__advantage {
  display: inline-block;
  max-width: 130px;
}

.product__advantage:first-child {
  margin-left: 111px;
}

.product__advantage:first-child:before {
  display: block;
  width: 35px;
  height: 35px;
  margin-top: -7px;
  margin-left: -38px;
  position: absolute;
  content: "";
  background-image: url("../assets/header-icons.png");
  background-position: 0 0;
}

.product__advantage:nth-child(2) {
  max-width: 103px;
  margin-left: 71px;
}

.product__advantage:nth-child(2):before {
  display: block;
  width: 33px;
  height: 35px;
  margin-top: -7px;
  margin-left: -35px;
  position: absolute;
  content: "";
  background-image: url("../assets/header-icons.png");
  background-position: -34px 0;
}

.product__advantage:nth-child(3) {
  max-width: 65px;
  margin-left: 68px;
}

.product__advantage:nth-child(3):before {
  display: block;
  width: 35px;
  height: 35px;
  margin-top: -7px;
  margin-left: -40px;
  position: absolute;
  content: "";
  background-image: url("../assets/header-icons.png");
  background-position: -64px 0;
}

.product__callback {
  display: block;
  width: 189px;
  height: 45px;
  margin-left: 236px;
  color: #ffffff;
  border: none;
  box-shadow: 3px 5px 9px rgba(57, 216, 76, 0.39);
  border-radius: 21px;
  background-color: #39d84c;
}

.product__callback:hover {
  transition: 0.2s;
  box-shadow: 0px 0px 9px 5px rgba(251, 250, 255, 0.77);
  cursor: pointer;
}

.product__callback-text {
  padding-left: 30px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 14px;
}

.product__callback-text:before {
  display: block;
  width: 21px;
  height: 21px;
  margin-top: -4px;
  margin-left: 7px;
  position: absolute;
  content: "";
  background-image: url("../assets/header-icons.png");
  background-position: -99px -7px;
}

.product__order-block {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
}

.product__banner {
  display: block;
  max-width: 370px;
  margin: 0 auto;
  text-align: center;
  color: #ffffff;
  font-family: "Myriad Pro", sans-serif;
  font-size: 48px;
  font-weight: 400;
  line-height: 50px;
}

.product__banner:after {
  display: block;
  margin-top: -12px;
  content: url("../assets/snake.png");
}

.product__description {
  display: block;
  max-width: 365px;
  margin: 0 auto;
  text-align: center;
  color: #c2c4eb;
  font-family: "Myriad Pro", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 30px;
}

.product__description span {
  color: #ffe200;
}

.product__items {
  display: block;
  position: relative;
  max-width: 600px;
  background: url("../assets/items-bg.png") no-repeat, transparent;
}

.product__item-name {
  display: block;
  max-width: 100px;
  float: left;
  position: relative;
  margin-top: 114px;
  margin-left: -11px;
  text-align: center;
  text-shadow: 7px 12px 5px rgba(22, 22, 45, 0.64);
  color: #fd5e7e;
  font-family: "Myriad Pro", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 24px;
  z-index: 1;
}

.product__item-name:after {
  display: block;
  content: url("../assets/arrow-to-product.png");
  margin-top: 10px;
  margin-left: 43px;
}

.product__kkc-black {
  display: inline-block;
  position: relative;
  padding-top: 63px;
  z-index: 0;
  left: -70px;
}

.product__plus-icon {
  display: inline-block;
  width: 62px;
  height: 62px;
  margin-top: 167px;
  margin-left: -98px;
  padding-top: 18px;
  position: absolute;
  text-align: center;
  color: #16162d;
  font-family: Roboto, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 24px;
  box-shadow: 1px 3px 13px rgba(238, 238, 238, 0.29);
  background-color: #eeeeee;
  border-radius: 50%;
  z-index: 2;
}

.product__box {
  display: inline-block;
  margin-left: -155px;
  position: relative;
  z-index: 3;
  top: -30px;
}

.product__fruits {
  display: inline-block;
  margin-left: -258px;
  position: relative;
  top: 20px;
  z-index: 4;
}

.product__box-comment {
  display: inline-block;
  height: 58px;
  box-shadow: 4px 7px 8px rgba(0, 0, 0, 0.32);
  background-color: #1e2229;
  background-image: linear-gradient(43deg, #ffffff 0%, #afafaf 59%, #ffffff 89%, #dedede 100%);
}

.product__box-comment.-box-med {
  width: 200px;
  margin-left: 84px;
  transform: skew(45deg);
  position: relative;
  top: -85px;
  z-index: 5;
}

.product__box-comment.-box-small {
  width: 186px;
  margin-top: -85px;
  margin-left: 80px;
  position: absolute;
  z-index: 4;
  clip-path: polygon(0% 0%, 75% 0, 100% 100%, 0% 100%);
  box-shadow: 4px 7px 8px rgba(0, 0, 0, 0.32);
}

.product__pearl-icon {
  display: inline-block;
  margin-top: -88px;
  margin-left: 48px;
  position: absolute;
  z-index: 5;
}

.product__attachment {
  display: block;
  color: #080e10;
  position: relative;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__attachment.-effect {
  max-width: 160px;
  margin-top: 12px;
  margin-left: 12px;
  white-space: pre;
  transform: skew(-45deg);
}

.product__attachment.-effect:before {
  display: block;
  margin-top: -28px;
  margin-left: -60px;
  content: url("../assets/effect-angle.png");
  position: absolute;
}

.product__attachment.-silver-drops {
  max-width: 95px;
  margin-top: 12px;
  margin-left: 50px;
}

.product__block {
  margin-top: 62px;
  margin-left: -54px;
}

.product__block.-form {
  display: block;
  max-width: 370px;
  height: 489px;
  margin-top: 86px;
  background-image: url("../assets/form-bg.png");
}

.product__sale {
  display: block;
  width: 70px;
  float: right;
  margin-top: 0;
  margin-right: 27px;
  padding-top: 4px;
  background-color: #e21e1e;
  color: #ededed;
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  letter-spacing: 0.35px;
}

.product__sale:before {
  display: block;
  content: '';
  position: absolute;
  margin-top: 29px;
  border: 35px solid transparent;
  border-radius: 1px;
  border-top: 20px solid #e21e1e;
}

.product__order-sale {
  clear: both;
  display: block;
  max-width: 261px;
  height: 120px;
  margin-top: 50px;
  margin-bottom: 0;
  margin-left: 53px;
  white-space: pre;
  text-align: center;
  color: #000000;
  font-family: Roboto, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 44px;
}

.product__order-sale span:first-child {
  font-size: 36px;
}

.product__order-sale span:last-child {
  position: relative;
  font-size: 36px;
  font-weight: 700;
  z-index: 1;
}

.product__order-sale span:last-child:before {
  display: block;
  width: 248px;
  height: 58px;
  position: absolute;
  margin-top: -58px;
  margin-left: 25px;
  content: "";
  box-shadow: 2px 14px 5px rgba(37, 42, 48, 0.29);
  border-radius: 4px;
  background-color: #ffe200;
  z-index: -1;
}

.product__price-container {
  display: block;
}

.product__price.-new {
  display: inline-block;
  margin: 0;
  padding-left: 26px;
  color: #d40619;
  font-family: "Roboto - Black", sans-serif;
  font-size: 48px;
  line-height: 48px;
  font-weight: 900;
}

.product__price.-new span {
  font-family: Roboto, sans-serif;
  font-weight: 700;
}

.product__price.-old {
  display: inline-block;
  color: #060606;
  margin: 0;
  padding-left: 80px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
}

.product__price.-old:before {
  position: absolute;
  margin-top: -30px;
  content: "обычная цена";
  color: #a6a6a6;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 48px;
}

.product__strike {
  display: block;
  margin-top: -35px;
  margin-left: 250px;
  width: 61px;
  height: 30px;
  position: absolute;
}

.product__name-container {
  display: block;
  padding-right: 24px;
  padding-left: 27px;
}

.product__name-container:nth-child(2) {
  margin-bottom: 14px;
}

.product__name-container span {
  display: block;
  color: #000000;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
}

.product__name-input {
  display: block;
  width: 318px;
  height: 47px;
  padding-left: 10px;
  box-shadow: 3px 4px 5px rgba(37, 42, 48, 0.47);
  border-radius: 4px;
  border: none;
  background-color: #e8e8e8;
  outline: none;
}

.product__submit-order {
  display: block;
  margin: 0 auto;
  padding: 27px 56px 24px 73px;
  box-shadow: 3px 4px 21px rgba(212, 6, 25, 0.47);
  border-radius: 4px;
  border: none;
  background-color: #d40619;
  color: #ffffff;
  text-align: center;
  font-family: "Myriad Pro", sans-serif;
  font-size: 24px;
  font-weight: 400;
}

.product__submit-order:hover {
  cursor: pointer;
  transition: 0.5s;
  background-color: #ff2f25;
}

@media all and (min-width: 1590px) {
  .product__order-block {
    padding-left: 340px;
  }
}

@media all and (max-width: 1190px) {
  .product {
    height: auto;
    background-image: none;
  }

  .product__navigation {
    justify-content: center;
    max-width: 540px;
  }

  .product__logo {
    margin-bottom: 10px;
  }

  .product__advantages-list {
    order: 2;
  }

  .product__advantage:first-child {
    margin-left: 0;
  }

  .product__callback {
    margin-left: 36px;
    order: 1;
  }
}

@media all and (max-width: 1010px) {
  .product__item-name {
    display: none;
  }
}

@media all and (max-width: 950px) {
  .product__block {
    margin-left: 0;
  }

  .product__block.-form {
    margin-top: 0;
    margin-bottom: 30px;
  }
}

@media all and (max-width: 580px) {
  .product {
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .product__navigation {
    max-width: 300px;
  }

  .product__advantage {
    display: block;
    order: 1;
  }

  .product__advantage:nth-child(n+1) {
    margin-left: 0;
    padding: 10px 0;
  }

  .product__callback {
    margin-bottom: 10px;
    margin-left: 0;
    order: 2;
  }

  .product__kkc-black {
    display: block;
    position: relative;
    float: none;
    padding-top: 63px;
    z-index: 1;
    left: 0;
  }

  .product__box-comment.-box-med {
    width: 200px;
    margin-left: 84px;
    transform: skew(45deg);
    position: absolute;
    top: 305px;
    z-index: 5;
  }

  .product__box-comment.-box-small {
    width: 186px;
    margin-top: -105px;
    margin-left: 120px;
  }

  .product__plus-icon {
    display: block;
    width: 62px;
    height: 62px;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 50%;
    padding-top: 18px;
    float: none;
    position: relative;
  }

  .product__box {
    display: block;
    margin-left: 0;
    z-index: 3;
    padding-top: 30px;
  }

  .product__fruits {
    display: none;
  }

  .product__pearl-icon {
    display: inline-block;
    margin-top: -108px;
    margin-left: 88px;
  }
}

@media all and (max-width: 460px) {
  .product__block.-form {
    display: block;
    max-width: 300px;
  }

  .product__order-sale {
    margin-left: 10px;
  }

  .product__price.-old {
    padding-left: 10px;
  }

  .product__strike {
    margin-left: 180px;
  }

  .product__name-input {
    width: 245px;
  }

  .product__submit-order {
    display: block;
    margin: 0 auto;
    padding: 15px 25px;
  }
}

