.main {
  display: block;
  width: 100%;
  background: url("../assets/main-bg.png") no-repeat;
  /*______________MEDIA RULES________________*/
}

.main__items-container {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
}

.main__item {
  display: block;
  max-width: 570px;
}

.main__item:last-child {
  margin-top: 123px;
  margin-left: 62px;
}

.main__item-title {
  display: block;
  margin-top: 111px;
  margin-bottom: 34px;
  color: #000000;
  font-family: "Myriad Pro", sans-serif;
  font-size: 48px;
  font-weight: 400;
  line-height: 48px;
  z-index: 2;
  position: relative;
}

.main__item-title:before {
  display: block;
  width: 321px;
  height: 75px;
  position: absolute;
  margin-top: -10px;
  margin-left: 200px;
  content: "";
  background-color: #ffe200;
  z-index: -1;
}

.main__article {
  display: block;
  max-width: 538px;
  color: #000000;
  white-space: pre;
  font-family: "Myriad Pro", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.main__donec {
  display: block;
  width: 100%;
}

.main__donec-bg {
  display: block;
  position: absolute;
  margin-top: -90px;
  margin-left: calc(50% - 395px);
  z-index: -1;
}

.main__donec-title {
  display: block;
  margin-top: 0;
  margin-bottom: 67px;
  color: #ffe200;
  text-align: center;
  font-family: "Myriad Pro", sans-serif;
  font-size: 48px;
  font-weight: 400;
  line-height: 48px;
}

.main__vitamines-container {
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
  justify-content: space-between;
  flex-wrap: wrap;
}

.main__vitamine-box {
  display: block;
  max-width: 565px;
  margin-bottom: 95px;
}

.main__vitamine-box:first-child {
  padding-left: 200px;
}

.main__vitamine-box:first-child:before {
  display: block;
  width: 180px;
  height: 175px;
  margin-left: -200px;
  content: "";
  position: absolute;
  background-image: url("../assets/4-layers.png");
  background-position: 0 0;
}

.main__vitamine-box:nth-child(2) {
  padding-left: 220px;
}

.main__vitamine-box:nth-child(2):before {
  display: block;
  width: 200px;
  height: 175px;
  margin-left: -200px;
  content: "";
  position: absolute;
  background-image: url("../assets/4-layers.png");
  background-position: -210px 0;
}

.main__vitamine-box:nth-child(3) {
  padding-left: 200px;
}

.main__vitamine-box:nth-child(3):before {
  display: block;
  width: 200px;
  height: 134px;
  margin-left: -200px;
  content: "";
  position: absolute;
  background-image: url("../assets/4-layers.png");
  background-position: 0 -250px;
}

.main__vitamine-box:nth-child(4) {
  padding-left: 220px;
}

.main__vitamine-box:nth-child(4):before {
  display: block;
  width: 200px;
  height: 150px;
  margin-left: -200px;
  content: "";
  position: absolute;
  background-image: url("../assets/4-layers.png");
  background-position: -210px -260px;
}

.main__vitamine-name {
  display: block;
  margin-top: 0;
  margin-bottom: 29px;
  color: #e0e0e0;
  font-family: "Myriad Pro", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 30px;
}

.main__vitamine-name.-margin {
  margin-bottom: 23px;
}

.main__about-vitamine {
  display: block;
  color: #787a9e;
  font-family: "Myriad Pro", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

@media all and (max-width: 1190px) {
  .main__donec-bg {
    display: none;
  }

  .main__vitamines-container {
    max-width: 570px;
  }

  .main__vitamine-box:nth-child(even) {
    padding-left: 200px;
  }
}

@media all and (max-width: 1120px) {
  .main__item:last-child {
    margin: 0;
  }
}

@media all and (max-width: 650px) {
  .main__item-title {
    max-width: 300px;
    font-size: 30px;
    margin-top: 60px;
    margin-left: 15px;
  }

  .main__item-title:before {
    width: 130px;
    height: 40px;
    margin-top: 7px;
    margin-left: 125px;
  }

  .main__article {
    white-space: normal;
    padding: 0 15px;
  }

  .main__item:last-child img {
    width: 300px;
  }

  .main__vitamines-container {
    max-width: 300px;
  }

  .main__vitamine-box {
    margin-bottom: 40px;
  }

  .main__vitamine-box:nth-child(-n+4) {
    padding-top: 200px;
    padding-left: 0;
  }

  .main__vitamine-box:nth-child(-n+4):before {
    margin-left: 0;
    margin-top: -170px;
  }
}

